@import url(//fonts.googleapis.com/css2?family=Roboto&display=swap);
.page{height:auto;width:100%;background-image:url(/static/media/bg-rm.5d8c646d.png);background-repeat:no-repeat;background-size:cover;background-position:center}.header{width:100%;height:250px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin-bottom:10px}.title{display:none}.form{height:150px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.form__input-text{height:40px;width:300px;background-color:black;color:white;border:2px solid aqua;padding:10px}.card-list{margin:0 50px 70px;display:grid;grid-template-columns:repeat(auto-fill, minmax(150px, 1fr));grid-gap:40px;gap:40px}.card-item{border:2px solid aqua;background-color:white;width:150px;height:245px;padding-bottom:20px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;border-radius:15px}.card-item:hover{box-shadow:9px 9px 48px 7px rgba(111,216,19,0.81)}.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.card__img{width:140px;margin-top:5px;border-radius:15px}.card__title{margin-top:10px;text-align:center}.card__description{width:auto;margin-top:20px;font-size:14px;background-color:aqua;padding:4px;border-radius:10px}.footer{background-color:black;height:50px;margin-top:50px;position:fixed;bottom:0;left:0;width:100%}*{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto', sans-serif}

